import React, {Component} from "react";
import {connect} from "react-redux";
import {
    clearDevicesList,
    redirectToComparator,
    clearErrorCode
} from "eshop/modules/simfree/actions/comparator";
import {
    getComparatorDevices,
    getErrorCode
} from "eshop/modules/simfree/selectors";
import OraModal from "eshop/modules/core/components/ui/Modal";

class ComparatorErrorModal extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <OraModal id={"comparator-error-modal"} open={this.props.errorCode > 0} onClose={this.props.clearErrorCode}
                      showClose={true} escapeClose={true} clickClose={true} size="medium">
                {this.props.errorCode === 1 && <h5 className="u-small-margin-right-m">{this.props.modalCategoryErrorMessage}</h5>}
                {this.props.errorCode === 2 && <h5 className="u-small-margin-right-m">{this.props.modalDevicesLimitErrorMessage}</h5>}
                <div className="u-padding-top-m">
                    <div className="l-row">
                        <div className="l-col l-col-12  u-right u-text-right">
                            <button className="opl-btn o-btn"
                                    onClick={() => this.props.clearDevicesList()}>
                                <span className="opl-ripple-box">
                                    <span className="opl-ripple-circle"/>
                                </span>{this.props.modalLeftButtonText}
                            </button>
                            <button className="opl-btn opl-btn--primary o-btn u-margin-left u-small-margin-top u-small-no-margin-l"
                                    disabled={this.props.comparatorDevices.length < 2}
                                    onClick={() => this.props.redirectToComparator(this.props.comparatorPageUrl)}>
                                <span className="opl-ripple-box">
                                    <span className="opl-ripple-circle"/>
                                </span>{this.props.modalRightButtonText}
                            </button>
                        </div>
                    </div>
                </div>
            </OraModal>
        )
    }
}

const mapStateToProps = state => ({
    comparatorDevices: getComparatorDevices(state),
    errorCode: getErrorCode(state)
});

const mapDispatchToProps = (dispatch) => ({
    clearDevicesList: () => dispatch(clearDevicesList()),
    redirectToComparator: (url) => dispatch(redirectToComparator(url)),
    clearErrorCode: () => dispatch(clearErrorCode())
});

export default connect(mapStateToProps, mapDispatchToProps)(ComparatorErrorModal);